html {
  background: #fafafa; }

html, body {
  margin: 0;
  padding: 0;
  font-family: roboto, sans-serif;
  -webkit-tap-highlight-color: transparent; }

pre,
code,
kbd,
samp,
tt {
  font-family: monospace;
  font-size: 1em; }

button:-moz-focus-inner {
  border: 0;
  padding: 0; }

button {
  cursor: pointer; }

.unbutton {
  display: inline-block;
  background: none;
  border: none;
  padding: 0;
  margin: 0;
  font: inherit;
  white-space: nowrap; }

.layout {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
  flex-flow: column;
  height: 100%; }

.main {
  -webkit-flex: 1;
  flex: 1;
  display: -webkit-flex;
  display: flex;
  overflow: hidden;
  position: relative; }

html,
body {
  height: 100%;
  overflow: hidden; }

.top-navbar-wrapper {
  z-index: 1;
  box-shadow: 0 0px 11px rgba(0, 0, 0, 0.4);
  width: 100vw;
  height: 3em; }
  .top-navbar-wrapper .top-navbar-container {
    height: 100%; }
    .top-navbar-wrapper .top-navbar-container .top-nav {
      display: -webkit-flex;
      display: flex;
      -webkit-flex-direction: row;
      flex-direction: row;
      list-style-type: none;
      margin: 0 0 0 5%;
      padding: 0 2% 0 0;
      -webkit-align-items: center;
      align-items: center;
      height: 100%;
      max-width: 85%;
      overflow: auto; }
      .top-navbar-wrapper .top-navbar-container .top-nav li {
        display: block;
        font-size: 4vw;
        white-space: nowrap;
        margin: 0;
        text-transform: uppercase; }
        .top-navbar-wrapper .top-navbar-container .top-nav li a {
          display: block;
